.Add-form-cover {
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	margin: auto;
	z-index: 2;
	background-color: rgba(0, 0, 0, 0.5);
}

.Add-form-wrapper {
	font-family: "Lato";
	position: absolute;
	left: 25%;
	right: 25%;
	top: 25%;
	width: 500px;
	height: 210px;
	margin: auto;
	background-color: rgba(50, 140, 193, 0.9);
	-webkit-box-shadow: 0px 0px 20px 8px rgba(0, 0, 0, 0.75);
	        box-shadow: 0px 0px 20px 8px rgba(0, 0, 0, 0.75);
	border-radius: 5px;
	color: white;
	text-shadow: 1px 1px black;
	padding: 20px;
}

.Instructions {
	margin: 10px;
}

.Add-form {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-direction: row;
	    flex-direction: row;
}

.Button-wrap {
	width: 500px;
	padding: 0;
	margin: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}

.Ok-button,
.Cancel-button {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: nowrap;
	    flex-wrap: nowrap;
	-ms-flex-align: center;
	    align-items: center;
	/*align-content: stretch;*/
}

.Ok-button {
	margin-right: 20px;
}

.Button-icon {
	width: 30px;
	height: 30px;
	background-color: #a6630a;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	text-shadow: none;
	font-size: 1.2rem;
}

.Form-button {
	margin: 0;
	font-size: 1rem;
	text-align: left;
	border: none;
	background: #ff9f1e;
	width: 80px;
	height: 30px;
	/*margin: 10px;*/
	color: white;
}

label {
	width: 230px;
	height: 30px;
	margin: 10px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: baseline;
	    align-items: baseline;
}

input {
	margin: 0 10px 0 10px;
	width: 130px;
	height: 20px;
}

select {
	width: auto;
	margin: 0;
}

.Add-form input::-webkit-input-placeholder {
	color: red;
}

.Add-form input:-ms-input-placeholder {
	color: red;
}

.Add-form input::-ms-input-placeholder {
	color: red;
}

.Add-form input::placeholder {
	color: red;
}

.Gender-label {
	width: 140px;
	margin-right: 5px;
}

.Gender-error {
	width: auto;
	height: 20px;
	line-height: 20px;
	margin: 10px 0 10px 0;
	padding: 0;
	color: #ff9f1e;
	text-shadow: none;
	font-weight: bold;
	font-size: 13px;
}

.Gender-label-wrapper {
	display: -ms-flexbox;
	display: flex;
}

@media only screen and (max-width: 1000px) {
	.Add-form-wrapper {
		width: 90%;
		left: 5%;
		right: 5%;
		padding: 0;
		height: auto;
	}
	/*.Ok-button {
		margin-left: 10px;
	}*/

	.Button-wrap {
		width: 100%;
		margin-bottom: 10px;
		margin-top: 10px;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-pack: center;
		    justify-content: center;
	}

	select {
		padding: 3px !important;
		margin-left: 29px;
	}
}



