.Navbar {
  width: 918px;
  height: 145px;
  margin-bottom: 20px;
  padding-left: 41px;
  padding-right: 41px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.Nav-button {
  background: #ff9f1e;
  width: 200px;
  margin-left: 0px;
  margin-right: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-line-pack: stretch;
      align-content: stretch;
  color: white;
}

.Nav-button div {
  padding: 6px;
}

.Nav-button div:first-child {
  /*margin: 8px;*/
  text-align: center;
  width: 22px;
  font-size: 1.2rem;
}

.Nav-button div:first-child {
  background-color: #a6630a;
}

.Nav-button div:last-child {
  line-height: 20px;
}

.Nav-button:last-child {
  margin-right: 0px;
}

.Nav-button:hover {
  background: #ff9100;
  cursor: pointer;
}

.Nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.Nav-list li {
  margin-top: 8px;
  padding: 2px;
}

.Nav-list li:hover {
  background: rgba(255, 255, 255, 0.1);
}

.Search-input,
.Search-select {
  border-radius: 0 !important;
  width: 165px;
  height: auto;
  border: 2px solid #a6630a;
  font-size: 1rem;
  background: #ff9f1e;
  margin-left: 0px;
  margin-right: 20px;
  padding: 6px;
  /*width: 100px;*/
  color: white !important;
}

::-webkit-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}

::-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: white;
}

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: white;
}

.Search-select {
  outline-style: none;
  padding: 6px !important;
}

.Search-select option {
  color: white;
}

@media only screen and (max-width: 1000px) {
  .Navbar {
    width: 100%;
    height: auto;
    padding: 0;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: stretch;
        align-items: stretch;
    margin-bottom: 5px;
  }

  .Nav-button {
    width: 100%;
    margin-top: 10px;
  }

  .Search-input,
  .Search-select {
    border: none;
    width: auto;
    padding-top: 4px;
    padding-bottom: 4px;
    margin: 0;
    background-color: #ffb756;
  }
}



