.App-content {
	/*background-image: linear-gradient(to bottom right, #0f2c3e, #5ba8d7);*/
	background-image: -webkit-gradient(linear, right top, left bottom, from(#2179bc), to(#105e95));
	background-image: -webkit-linear-gradient(top right, #2179bc, #105e95);
	background-image: -o-linear-gradient(top right, #2179bc, #105e95);
	background-image: linear-gradient(to bottom left, #2179bc, #105e95);
	display: -ms-flexbox;
	display: flex;
	-ms-flex: 1 0 auto;
	    flex: 1 0 auto;
	/*for IE */
	-ms-flex-positive: 1;
	    flex-grow: 1;
	-ms-flex-negative: 0;
	    flex-shrink: 0;
	-ms-flex-preferred-size: auto;
	    flex-basis: auto;
	/**/
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	padding-top: 40px;
	border-top: 1px solid black;
	border-bottom: 1px solid black;
}

@media only screen and (max-width: 1000px) {
	.App-content {
		padding: 0.5rem;
	}
}



